/* movielist vss */
.root-wrap {
  height: 230vh;
}

/* movie css */
.Movie {
  font-family: "Happiness-Sans-Bold";
  padding: 17px;
  cursor: pointer;
}
.Movie:hover {
  filter: drop-shadow(2px 2px 10px white);
  opacity: 0.8;
}
.Movie_Columns_top {
  line-height: 0;
}
.Movie_Columns_top img {
  border-radius: 10% 10% 0 0;
}
.Movie_Columns_bottom {
  background: #097185;
  border-radius: 0 0 20px 20px;
  height: 5rem;
}
.Movie_Columns_bottom_title {
  font-size: 1.3rem;
  padding: 0.5rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 13rem;
}
.Movie_Columns_bottom_rating {
  padding: 0 0.5rem;
  vertical-align: middle;
}
.Movie_Columns_bottom_rating span {
  color: yellow;
  font-size: 1.2rem;
  padding: 0 0.3rem 0 0;
}

/* movie detail */
.movie-detail-wrap {
  font-family: "Pretendard-Regular";
  width: 40%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 4rem;
  filter: drop-shadow(7px 9px 23px black);
}
.movie-detail-wrap img {
  width: 65%;
  height: 30rem;
  object-fit: cover;
}
.movie-detail-explain {
  width: 65%;
  background: #1f1f1f;
  padding: 16px 24px 24px 24px;
  position: relative;
}
.movie-detail-explain::before {
  content: "";
  width: 100%;
  height: 53%;
  background: linear-gradient(0deg, #1f1f1f 23%, transparent);
  display: block;
  position: absolute;
  top: -48%;
  left: 0%;
}
.movie-detail-explain div {
  padding-bottom: 8px;
}
.movie-detail-title {
  font-family: "Happiness-Sans-Bold";
  font-size: 2em;
  letter-spacing: 2px;
  margin-bottom: 24px;
  font-weight: bold;
}
.movie-detail-title-subject {
  color: red;
  padding-right: 16px;
  padding-bottom: 12px;
}
.movie-detail-explain .star {
  color: yellow;
}
.genres-div {
  display: flex;
}
.genres-container .genre-span {
  display: flex;
  color: white;
}
.movie-detail-url a {
  display: flex;
  justify-content: center;
  padding: 30px 0;
}
.movie-detail-url-button {
  width: 80%;
  height: 3rem;
  background: #097185;
  border-radius: 10px;
  box-shadow: 3px 3px 3px white;
  font-size: 1.2rem;
  text-align: center;
  line-height: 3rem;
  transition: 0.3s;
}
.movie-detail-url-button:active {
  box-shadow: none;
  margin-left: 5px;
  margin-top: 5px;
}
.movie-detail-url-button:hover {
  background: rgb(115, 150, 157);
}

/* responsive -tablet */
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .movie-detail-wrap {
    width: 70%;
  }
}

/* responsive -mobile */
@media screen and (max-width: 479px) {
  .movie-detail-wrap {
    width: 100%;
  }
}
