* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background: #252451;
  color: white;
}
a {
  color: white;
  text-decoration: none;
}
/* header */
.header_container {
  width: 100%;
  height: 440px;
  display: flex;
  justify-content: center;
}
.header-wrap {
  width: 40%;
  padding: 8rem;
}
.header-left-wrap img {
  max-width: 100%;
}
.header-right-wrap {
  display: flex;
  justify-content: center;
}
.header-right-wrap span {
  font-family: "Pretendard-Regular";
  padding: 80px 0 8px 0;
  border-bottom: 1px solid white;
}

.App {
  display: flex;
  flex-wrap: wrap;
  width: 70%;
  margin: 0 auto;
}
.loading_text {
  font-family: "Happiness-Sans-Bold";
  font-size: 3rem;
  margin: 0 auto;
  color: red;
}

/* responsive -tablet */
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .header-wrap {
    width: 83%;
    padding: 8rem 4rem;
  }
}

/* responsive -mobile */
@media screen and (max-width: 479px) {
  .header-wrap {
    width: 100%;
    padding: 8rem 4rem;
  }
  .header-right-wrap span {
    font-size: 0.9rem;
  }
  .App {
    width: 60%;
  }
  .root-wrap {
    height: 1000vh;
  }
}
